.homepage{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 50vw;
    height: 50vh;
    border-radius: 20px;
    box-shadow: 0 0 15px 5px gray;
    background: white;
    position: relative;
}
.homepage > .button{
    background:#5a9bf0 ;
    border: 2px solid #00060c;
    color: #fff;
    font-size: 1.25rem;
    padding: 0.5rem;
    margin: 0.5rrem 0;
    border-radius: 8px;
    outline: none;
    cursor: pointer;
    transition-duration:0.4s ;
}
.homepage > .button:hover{
    background-color: #1d2129;
    transition-duration:0.1s ;
    border: 2px solid #5a9bf0;
}